<template>
  <div :class="$style.user">
    <div :class="$style.avator"></div>
    <div :class="$style.authorInfo">
      <div :class="$style.name">
        {{user.name}}
        <div :class="$style.publishTime">
          {{createTime}}
        </div>
      </div>
      <div :class="$style.trueInfo">
        <div :class="$style.trueName">
          
        </div>
        <div class="unit">
          
        </div>

      </div>
    </div>
  </div>
</template>

<script>

// timeType: 1.      2.n

export default {
  name: 'user',
  props: {
    user: Object,
    publishTime: {
      type: null,
    },
    timeType: {
      type: Number,
      default: 1,
    }
  },
  computed: {
    createTime() {
      if (!this.publishTime) {
        return
      }
      if (this.timeType === 1) {
        let time = new Date(this.publishTime);
        let timeText = `${time.getFullYear()}${time.getMonth()}${time.getDate()} ${time.getHours()}${time.getMinutes()}`;
        return timeText;
      } else if (this.timeType === 2) {
        let createTime = this.publishTime;
        let timepass = this.currentTime - createTime;
        let timeText = '';
        if (timepass < 60000) {
          let s = Math.floor(timepass / 1000)
          timeText = `${s} `;
        } else if (timepass < 3600000) {
          let m = Math.floor(timepass / 1000 / 60);
          timeText = `${m} `;
        } else if (timepass < 86400000) {
          let h = Math.floor(timepass / 1000 / 3600);
          timeText = `${h} `;
        } else {
          let time = new Date(createTime);
          timeText += time.getFullYear() + '' + time.getMonth() + '' + time.getDate() + '';
        }
        return timeText;
      }
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style module>
  .user {
    display: flex;
    align-items: center;
  }
  .authorInfo {
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 24px;
    margin-left: 10px;
  }
  .avator {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #ccc;
  }
  .name {
    display: flex;
    margin-bottom: 5px;
  }
  .publishTime {
    display: flex;
    align-items: flex-end;
    color: var(--);
    font-size: 20px;
    margin-left: 10px;
  }
  .trueInfo {
    display: flex;
  }
  .trueName {
    margin-right: 10px;
  }
</style>



