<template>
  <div :class="$style.detailWrapper">
    <div :class="$style.contentType">
      <div :class="$style.title">
        {{tittleText}}
      </div>
      <div class="search">
        
      </div>
      <div class="attention">
        <mytitle text=''/>
      </div>
      <div class="hot">
        <mytitle text=''/>
      </div>
    </div>
    <div :class="$style.action" >
      <div :class="$style.next">
        <div :class="$style.button" @click='handlePublish'>
          {{buttonText}}
        </div>
      </div>
      <div :class="$style.secret" v-if='this.contentType === 1'>
        <router-link to="/find"  :class="$style.button">
          
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import mytitle from '../../components/MyTitle';

export default {
  name: 'contentDetail',
  components: {
    mytitle,
  },
  data() {
    return {
      id: this.$route.params.id,
    }
  },
  computed: {
    contentType() {
      const type = this.$route.params.type;
      if (type === 'article') {
        return 1
      } else if (type === 'issue') {
        return 2
      }
    },
    tittleText() {
      const type = this.contentType
      if (type === 1) {
        return ''
      } else if (type === 2) {
        return ''
      }
    },
    buttonText() {
      const type = this.contentType
      if (type === 1) {
        return ' '
      } else if (type === 2) {
        return ''
      }
    }
  },
  methods: {
    handlePublish() {
      // TODO:   action
      // 
      // 1. 
      // 2. chooiseTopicactionxdispatch('chooiseTopic', {: })
      // 3. chooiseTopicaction
      // 4.  /find
      this.$router.push('/find')
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style module>
  .detailWrapper {
    composes: fullScreen from "../../commenStyle/layout.css";
    background-color: #fff;
  }
  .contentType {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-bottom: 168px;
  }
  .action {
    position: fixed;
    bottom: 0;
    display: flex;
    width: 100%;
    box-shadow: 0 2px 15px 1px #aaa;
  }
  .secret, .next {
    padding: 30px;
  }
  .secret {
    flex: 0 0 50%;
  }
  .next {
    flex: 1;
  }
  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    padding: 30px;
    border-radius: 10px;
    background-color: var(--);
    color: #fff;
  }
</style>



